  const handleImportExcel = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
    
    setLoading(true);
    setImportProgress({ show: true, current: 0, total: 0, fileName: file.name });
    
    const reader = new FileReader();
    reader.onload = async (event) => {
      try {
        const data = new Uint8Array(event.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet);
        
        //   
        setImportProgress(prev => ({ ...prev, total: jsonData.length }));
        
        let imported = 0;
        for (let i = 0; i < jsonData.length; i++) {
          const row = jsonData[i];
          const acc = row[' '] || '';
          const name = row[''] || '';
          
          if (!acc.toString().trim() || !name.toString().trim()) continue;
          
          const client = {
            fullName: (row[''] || '').toString().trim(),
            settlement: (row[' '] || '').toString().trim(),
            streetType: (row[' '] || '').toString().trim(),
            street: (row[''] || '').toString().trim(),
            building: (row[''] || '').toString().trim(),
            buildingLetter: (row[' .'] || '').toString().trim(),
            apartment: (row[''] || '').toString().trim(),
            apartmentLetter: (row[' .'] || '').toString().trim(),
            accountNumber: acc.toString().trim(),
            eic: (row['EIC'] || '').toString().trim(),
            phone: (row[''] || '').toString().trim(),
            meterBrand: (row[' '] || '').toString().trim(),
            meterSize: (row[''] || '').toString().trim(),
            meterNumber: (row[' '] || '').toString().trim(),
            meterYear: (row[' '] || '').toString().trim(),
            verificationDate: (row[' '] || '').toString().trim(),
            nextVerificationDate: (row[' '] || '').toString().trim(),
            installationDate: (row[' '] || '').toString().trim(),
            meterLocation: (row[' '] || '').toString().trim(),
            meterGroup: (row[' .'] || '').toString().trim(),
            meterSubtype: (row[''] || '').toString().trim(),
            meterType: (row[' .'] || '').toString().trim(),
            meterOwnership: (row[''] || '').toString().trim(),
            serviceOrg: (row['..'] || '').toString().trim(),
            mvnssh: (row[''] || '').toString().trim(),
            rsp: (row[''] || '').toString().trim(),
            seal: (row[''] || '').toString().trim(),
            stickerSeal: (row[' '] || '').toString().trim(),
            boilerBrand: (row[' '] || '').toString().trim(),
            boilerPower: (row[' '] || '').toString().trim(),
            stoveType: (row['  '] || '').toString().trim(),
            stoveCount: (row[' '] || '').toString().trim(),
            columnType: (row[' '] || '').toString().trim(),
            columnCount: (row[' '] || '').toString().trim(),
            area: (row[''] || '').toString().trim(),
            utilityType: (row['. -'] || '').toString().trim(),
            utilityGroup: (row[''] || '').toString().trim(),
            grs: (row[''] || '').toString().trim(),
            gasDisconnected: (row[' '] || '').toString().trim(),
            disconnectMethod: (row[' '] || '').toString().trim(),
            disconnectSeal: (row[' .'] || '').toString().trim(),
            disconnectDate: (row[' .'] || '').toString().trim(),
            connectDate: (row[' .'] || '').toString().trim(),
            dacha: row[''] === '' || row[''] === true,
            temporaryAbsent: row[' '] === '' || row[' '] === true
          };
          
          await addClient(client);
          imported++;
          
          //  
          setImportProgress(prev => ({ ...prev, current: i + 1 }));
        }
        
        await loadClients();
        await loadTotalCount();
        await loadSettlements();
        await loadStreets();
        await loadMeterData();
        
        //  
        setImportProgress({ show: false, current: 0, total: 0, fileName: '' });
        setIsInitialLoading(false); //    loader   
        showToast('success', ` ${imported} !`, 4000);
      } catch (error) {
        console.error('Import error:', error);
        setImportProgress({ show: false, current: 0, total: 0, fileName: '' });
        showToast('error', '   ');
      }
      setLoading(false);
    };
    reader.readAsArrayBuffer(file);
    e.target.value = '';
  };

