      meterSubtype: '', meterType: '', meterOwnership: '', serviceOrg: '', mvnssh: '', 
      rsp: '', seal: '', stickerSeal: '',
      boilerBrand: '', boilerPower: '', stoveType: '', stoveCount: '', columnType: '', columnCount: '',
      area: '', utilityType: '', utilityGroup: '', grs: '',
